
import { Component, ViewChild } from '@angular/core';
import {AlertController,LoadingController, ViewController,IonicPage,  NavController,Events , ActionSheetController, NavParams } from 'ionic-angular';
import {   FormBuilder,  FormGroup } from '@angular/forms';
import { Auth,NotificationProvider } from '../../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';


@IonicPage()
@Component({
  selector: 'page-asignacioncalusac102a-create',
  templateUrl: 'asignacioncalusac102a-create.html',
})
export class Asignacioncalusac102aCreatePage {
 
  @ViewChild('fileInput') fileInput;
  public form                  : FormGroup;
  username: string = "";
  public nombre                 : any;
  public codigounidad                 : any;
  tiposus: any=[];
  public iddia2		  :any;
  public idhora2		  :any;
  public idhoraf2		  :any;
  public idperiodo2		  :any=[];
  personalid: string = "";
  public iddia		     : any;
  public idtipo		     : any;
  public idhora		     : any;
  public idhoraf		     : any;
  public ididioma		     : any;
  public ididioma2		     : any;
  dataxx: any=[];
ordenpago:any='';
llave:any='';
rubro:any='45';
ano:any='2019'
  public fecha		     = (new Date()).toISOString();
  public identificador		     : any;
  public periodo		     : any;
  items: any=[];
  resultado: any=[];
  idempresa:any;
  public vgrupo  :any=[];
  public vgrupo2  :any=[];
  public vgrupo3  :any=[];
  cui:''
 
  titulo2:any;

  //private _ID                  : String;
  public pageTitle           	: string;
  public image              	: any;
  novp:any;
  isDisablednit :any;
  constructor( public alertCtrl: AlertController,private loadingCtrl: LoadingController,public storage: Storage, public msj: NotificationProvider, public translateService: TranslateService,public viewCtrl: ViewController,public authService:  Auth,public navCtrl 		: NavController,  
    public navParams 	: NavParams,   private _FB          : FormBuilder,         public actionSheetCtrl: ActionSheetController, 
          public events: Events)
  {
     // Define form validation - VERY basic!
     this.form = this._FB.group({

       iddia     : [''],
       idhora     : [''],
       idhoraf     : [''],
      
       
     });

     this.titulo2='Sede'

     this.translateService.get('APPX').subscribe((value) => {
      this.idempresa=value
     
  })
 

  
  }



  ngOnInit() {

    this.storage.get('username').then((value) => {
      this.username = value;
  }); 
    
  this.novp =	this.navParams.data.nov;







  


}
    
  ionViewDidLoad() : void
  {

    
    this.dataxx=this.navParams.data.datax

    this.iddia=this.dataxx.n1
    this.idhora=this.dataxx.n2
    this.idhoraf=this.dataxx.n4

  


  }

  
  manageGallery2(loading) : void
  {
    let creat='Actualiza';


    if(this.username=='')
    {
      this.msj.showError3('Mensaje','Existe un problema con su dispositivo , no existe usuario');
      loading.dismiss();
      return;
    }    
    if(!this.username)
    {
      this.msj.showError3('Mensaje','Existe un problema con su dispositivo , no existe usuario');
      loading.dismiss();
      return;
    }
  
    

   
     let   options       : any	     = { 
      operacion:'ponenota2',
      n1: this.iddia,
      n2: this.idhora,  
      n3: Number(this.iddia)+Number(this.idhora),
      n4: this.idhoraf,
      n5: Number(this.iddia)+ Number(this.idhora)+ Number(this.idhoraf),

     bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' asigna nota examen de bimestre '}
    };
      

          if(options){

            this.authService.createReg2b(this.dataxx._id,options,'asignaubicacions').then((result) => {
              loading.dismiss();
              if (!this.form.valid) { return; }
              this.viewCtrl.dismiss(result);
          }, (err) => {  
            loading.dismiss();
            this.msj.showError3('Mensaje',err.error);
          });
          

        }
    
  }
  manageGallery() : void
  {

    

    if(this.username=='')
    {
      this.msj.showError3('Mensaje','Existe un problema con su dispositivo , no existe usuario');
     
      return;
    }    
    if(!this.username)
    {
      this.msj.showError3('Mensaje','Existe un problema con su dispositivo , no existe usuario');
    
      return;
    }
    





    const loading = this.loadingCtrl.create({
      spinner: 'bubbles',
      content: "Espere un momento,  Realizando  Operaci√≥n ......"
    });
    loading.present();

    this.manageGallery2(loading);



  }

  
  cancel2() {
    this.identificador=''
    this.iddia=''
    this.idtipo=''
    this.iddia2=''
    this.idhora=''
    this.nombre=''
    this.items=[]
  }
  cancel() {
    this.viewCtrl.dismiss();
  }
  done() {
    if (!this.form.valid) { return; }
    //this.viewCtrl.dismiss(this.form.value);   
  }

  onSelectChange(selectedValue: any) {
        
        this.iddia2=selectedValue.split(',');

      
  


  }
     

  onSelectChange2(selectedValue: any) {   
    this.idhora2=selectedValue.split(',');
  
 
  }
 

  
  onSelectChange3(selectedValue: any) {
    this.idhoraf2=selectedValue.split(',');
  
 
  }


}



